import { Form } from './Form';
export class ContactForm extends Form {
    initElements() {
        super.initElements();
        this._email = this.getElement('[name="email"]');
        this._phone = this.getElement('[name="phone"]');
    }
    set email(value) {
        this._email.value = value;
    }
    set phone(value) {
        this._phone.value = value;
    }
}
