import { View } from '../base/View';
import { createElement } from '../../utils/utils';
export class Basket extends View {
    constructor(container, events) {
        super(container);
        this.events = events;
    }
    initElements() {
        this._list = this.getElement('.basket__list');
        this._total = this.getElement('.basket__price');
        this._button = this.getElement('.basket__button');
    }
    initEvents() {
        this._button.addEventListener('click', () => {
            this.events.emit('order:open');
        });
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста',
            }));
        }
    }
    set total(total) {
        this.setText(this._total, total ? total.toString() + ' синапсов' : '');
    }
}
