import { Model } from './base/Model';
export class Order extends Model {
    constructor(data, events) {
        super(data, events);
        this.errors = [];
        this.items = [];
    }
    /**
     * Update order according to the data.
     *
     * @param {object} data
     *   Order data.
     */
    update(data) {
        Object.assign(this, data);
    }
    ;
    /**
     * Set new order status.
     *
     * @param {OrderStatus} status
     *   New order status.
     */
    setStatus(status) {
        this.status = status;
    }
    /**
     * Get order status.
     *
     * @returns {string}
     *   Order status.
     */
    getStatus() {
        return this.status;
    }
    /**
     * Add new order error.
     *
     * @param {string} error
     *   Order error message.
     */
    addError(error) {
        this.errors.push(error);
    }
    /**
     * Get order errors.
     *
     * @returns {string[]}
     *   Order errors.
     */
    getErrors() {
        return this.errors;
    }
}
