import { Api } from './base/api';
export class ShopApi extends Api {
    constructor(baseUrl, cdnUrl, events) {
        super(baseUrl);
        this.cdnUrl = cdnUrl;
        this.events = events;
    }
    getProducts() {
        return this.get('/product').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdnUrl + item.image }))));
    }
    sendOrder(data) {
        return this.post('/order', data).then((response) => response);
    }
}
